import React from "react";
import { useRecoilValue, useSetRecoilState } from "recoil";
import { Categories, IToDo, NewCategoryState, toDoState } from "../atoms";

/* 
  [
    {
        "text": "5",
        "id": 1660538626206,
        "category": "TO_DO"
    },
    {
        "text": "4",
        "id": 1660538625900,
        "category": "TO_DO"
    },
    {
        "text": "3",
        "id": 1660538625408,
        "category": "TO_DO"
    },
    {
        "text": "2",
        "id": 1660538625090,
        "category": "TO_DO"
    },
    {
        "text": "1",
        "id": 1660538624744,
        "category": "TO_DO"
    }
]
  Q. category     ? 
  A. 1.('state' mutate !)  toDo 'index'    
     2.    to do   
      > targetindex, oldToDo     
     3. "reaplce the to do in the index 'oldToDo' with 'newToDo' "
  */
function ToDo({text, category, id}:IToDo) {
  const NewCategories = useRecoilValue(NewCategoryState)
  const setToDos = useSetRecoilState(toDoState);
  
  const onClick = (event:React.FormEvent<HTMLButtonElement>) => {
    const {
      currentTarget: {name}, 
    } = event; 
    setToDos((oldToDos ) => {
      const targetIndex = oldToDos.findIndex((toDo) => toDo.id === id );
      const newToDo = {text:text, id, category: name as any } 

      return [ 
        ...oldToDos.slice(0, targetIndex),
        newToDo,
        ...oldToDos.slice(targetIndex + 1)
      ]
    } ) 

  };
  
  const CATEGORIES = [...Object.keys(Categories), ...NewCategories]
  // category  (todo    )  ''      
  return (
    <li>
      <span>{text}</span>
        {CATEGORIES.map(
          (key: any, index:number) => 
          category !== key && (
            <button key={`button-${index}`} name={key} onClick={onClick}> {key}</button>
          )
        )}
    </li>
  )
}
export default ToDo;